<template>
  <div class="EBCCalculator">
    <h3>1. Combien de litre de bière ?</h3>
    <div class="Input">
      <span for="beerL">Mon brassin fait <input type="number" id="beerL" v-model="$store.state.beerL" onClick="this.select();" placeholder="20">L de bière</span>
    </div>

    <h3>2. Quels malt sont utilisés ?</h3>
    <table class="EBCCalculator__table">
      <tr>
        <!-- <th>Nom du malt</th> -->
        <th></th>
        <th>Qt. de Malt (en g)</th>
        <th>EBC du Malt</th>
        <th>MCU</th>
      </tr>
      
      <tr v-for="(malt, key) in malts" :key="key">
        <td><button v-show="malts.length >= 2" @click="destroyMalt(key)">x</button>
        <td><input type="number" step="100" id="maltQt" v-model="malt.maltQt" placeholder="ex: 1720"></td>
        <td><input type="number" id="ebc" v-model="malt.ebc" placeholder="ex: 32"></td>
        <td>{{ getMCU(malt) }}</td>
      </tr>
      <tr>
        <td></td>
        <td colspan="3"><button @click="addMalt()" class="Add">+ Ajouter un autre type de malt <img :src="require('../assets/malt.svg')"/></button></td>
      </tr>
    </table>
    <p style="height: 0;overflow:hidden;">Total MCU : {{ totalMCU }}</p>
    <p style="height: 0;overflow:hidden;">Total EBC : {{ totalEBC }}</p>
  </div>
</template>

<script>

export default {
  name: 'EBCCalculator',
  data: function () {
    return {
      malts: [{ebc:null,maltQt:null}],
      // maltQt: null,
      // ebc: null
    }
  },
  computed: {
    totalMCU: function () {
      let totalMCU = 0;
      this.malts.forEach(malt => { totalMCU += this.getMCU(malt) })
      return Math.round(totalMCU * 10) / 10;
    },
    totalEBC: function () {
      let ebc = Math.round(2.94 * Math.pow(this.totalMCU, 0.6859));
      this.$store.commit ('updateEBC', ebc)
      return ebc;
    },
  },
  methods: {
    getMCU(malt) {
      return Math.round((4.24 * malt.ebc * (malt.maltQt / 1000) / this.$store.state.beerL) * 10) / 10
    },
    addMalt() {
      this.malts.push({ebc:null,maltQt:null})
    },
    destroyMalt(id) {
      this.malts.splice(id, 1);
    },
    // getHexEBC() {
    //   var ebc2hex = new Ebc2Hex;
    //   return ebc2hex.convert(this.totalEBC, 5);
    // },
    convert() {
      let ebc = this.totalEBC;
      let sat = 1
        ebc = ebc > 80 ? 80 : ebc;
        ebc = ebc < 0 ? 0 : ebc;
        var srm = this.ebcToSrm(ebc);
        var rgb = {
            r: this.calcRed(srm),
            g: this.calcGreen(srm),
            b: this.calcBlue(srm)
        };
        rgb = this.desaturate(rgb, sat);
        
        return this.rgbToHex(rgb);
    },
    ebcToSrm: function(ebc) {
        return ebc * 0.508;
    },
    calcRed: function(srm) {
        var result = Math.round(280 - srm * 5.65);
        return result > 255 ? 255 : result;
    },
    calcGreen: function(srm) {
        var result = Math.round(0.188349 * Math.pow(srm, 2) - 13.2676 * srm + 239.51);
        return result;
    },
    calcBlue: function(srm) {
        var result = Math.round(0.000933566 * Math.pow(srm, 4) - 0.0894788 * Math.pow(srm, 3) + 3.00611 * Math.pow(srm, 2) - 40.8883 * srm + 183.409);
        return result < 0 ? 0 : result;
    },
    segmentToHex: function(seg) {
        var hex = seg.toString(16);
        return hex.length == 1 ? "0" + hex : hex;
    },
    rgbToHex: function(rgb) {
        return "#" + this.segmentToHex(rgb.r) + this.segmentToHex(rgb.g) + this.segmentToHex(rgb.b);
    },
    desaturate: function(rgb, sat) {
        var gray = rgb.r * 0.3086 + rgb.g * 0.6094 + rgb.b * 0.0820;
        gray = gray * (1 - sat);
        rgb.r = Math.round(rgb.r * sat + gray);
        rgb.g = Math.round(rgb.g * sat + gray);
        rgb.b = Math.round(rgb.b * sat + gray);
        return rgb;
    }
  }
  // components: {
  //   EBCMaltRow
  // },
}
</script>

